import { Component, OnInit, EventEmitter, Output, OnDestroy } from '@angular/core'
import { Frase } from '../shared/frase.model'
import { FRASES } from './frase-mock'

@Component({
  selector: 'app-painel',
  templateUrl: './painel.component.html',
  styleUrls: ['./painel.component.css']
})
export class PainelComponent implements OnInit, OnDestroy {
  public instrucao : string = 'Traduza a frase:'
  public frases : Frase[] = FRASES 
  public quantFrases : number = this.frases.length
  public resposta : string = ''
  public rodada : number = 0
  public rodadaFrase : Frase
  public progresso : number = 0
  public tentativas : number = 3

  // Propriedade de componente filho pra Pai (app.component)
  @Output() public encerrarJogo : EventEmitter<string> =  new EventEmitter()

  constructor() { 
    this.atualizaRodada()
  }
      
  ngOnInit() {

  }

  ngOnDestroy(){
    // para destruir o componente painel EventEmitter
  }

  public atualizaResposta(resposta : Event) : void {
      this.resposta = (<HTMLInputElement>resposta.target).value.trim()
  }

  public verificarResposta() : void {
    this.resposta.trim()
    if (this.rodadaFrase.frasePtBr == this.resposta.trim()) {
      this.rodada++
      this.progresso = this.progresso + (100 / this.quantFrases) 
       if (this.rodada === this.quantFrases) {
        this.encerrarJogo.emit('vitoria')
      }

      this.atualizaRodada()
      
    } else {
      this.tentativas--
      if(this.tentativas === -1){
        this.encerrarJogo.emit('derrota')
      }
    }
    
  }

  public atualizaRodada() : void {
    this.rodadaFrase = this.frases[this.rodada]
    this.resposta = ''
  }

}



